@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;400;600;900&display=swap");
* {
  margin: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  box-sizing: border-box;
  font-family: "Source Sans Pro", sans-serif;
}
.App {
  display: flex;
  flex-direction: row;
  overflow: hidden;
}
.App > div {
  width: 100%;
  height: 100vh;
  padding: 30px;
  overflow-x: hidden;
}
:root {
  --dark: #11353e;
  --light: #3dcbea;
  --white: #fff;
  --green: #a9ae5b;
  --shadow: #dcdcdc;
  --Space: 20px;
}
header {
  width: 60px;
  height: 100vh;
  z-index: 1000;
  top: 0;
  left: 0;
  background-color: var(--white);
  color: var(--dark);
}
header nav {
  width: 100%;
  height: 100%;
}
header nav ul {
  display: flex;
  list-style: none;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
}
header nav ul li {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
}
header nav ul li .icon {
  position: absolute;
  transition: 0.5s;
  aspect-ratio: 1/1;

  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

header nav ul li a {
  color: inherit;
  text-decoration: none;
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

header nav ul li:hover .icon,
header nav ul li a.active .icon {
  color: var(--green);
  width: 100%;
}

/* end Header */
/* start Home */
.Home {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  overflow-y: scroll;
  overflow-x: hidden;
}
.Home .text h1 {
  font-size: 2.8rem;
  color: var(--dark);
  padding-block: calc(var(--Space) / 2);
}
.Home .text p {
  font-size: 1.5rem;
  color: var(--green);
}
.Home .text {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.Home img {
  width: 100%;
  position: relative;
  animation-name: updown;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate-reverse;
  transition: 0.5s;
}
@keyframes updown {
  from {
    top: 20px;
  }
  to {
    top: -20px;
  }
}
/* End Home */
.projects {
  overflow-y: scroll;
  white-space: wrap;
  overflow-x: hidden;
}
.projects .container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  justify-content: space-evenly;
  align-items: center;
  gap: 10px;
}
.projects .container .item {
  text-decoration: none;
  color: var(--dark);
  padding: 10px;
  border-radius: 5px;
  transition: 0.5s;
  display: block;
  width: 100%;
  border: var(--shadow) 0.5px solid;
}

.projects .container .item img {
  width: 100%;
  aspect-ratio: 16/8;
  border-radius: 5px;
}
.projects .container .item .project-name {
  font-size: 25px;
  padding-block: calc(var(--Space) / 2);
  font-weight: 700;
  letter-spacing: 1.2px;
}
.projects .container .item .project-about {
  font-size: 20px;
  padding-block: calc(var(--Space) / 3);
  color: var(--green);
}
.projects .container .item .project-langs {
  color: var(--light);
}
.projects h1 {
  padding-bottom: var(--Space);
  margin-top: calc(-0.5 * var(--Space));
  text-align: center;
  font-size: 40px;
  color: var(--dark);
}
/* End Projects */
/* Start About me */
.about-me {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 5px;
}
.about-me h2 {
  color: var(--dark);
  margin-block: calc(var(--Space) / 6);
  text-align: center;
}
.about-me .skills-one .container {
  width: 100%;
  display: flex;
  flex-direction: column;

  height: 100%;
  justify-content: space-around;
}
.about-me .container .skill {
  width: 100%;
  height: 30px;
  color: var(--dark);
  position: relative;
}
.about-me .container .skill .skill-name {
  position: absolute;
  left: 50%;
  font-size: 14px;
  top: 50%;
  color: var(--white);
  transform: translate(-50%, -50%);
}
.about-me .container .skill .skill-range {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--light);

  z-index: -1;
  top: 0;
  left: 0;
  border-radius: 10px;
  width: 100%;
}
.about-me .container .skill:nth-child(odd) .skill-range {
  background-color: var(--green);
}
.about-me .container .skill:nth-child(odd) .skill-name {
  color: var(--dark);
}
.about-me .skills-one {
  width: 49%;
}
.about-me .skills-two {
  width: 49%;
}
.about-me .skills-two .other-skills span {
  width: fit-content;
  background-color: var(--green);
  color: var(--dark);
  padding: 2.5px 5px;
  border-radius: 5px;
  margin: 5px;
  display: inline-block;
}
.about-me .skills-two .other-skills span:nth-child(even) {
  color: var(--white);
  background-color: var(--light);
}
.about-me .skills-two .langs {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.about-me .langs .container {
  display: flex;
  gap: 10px;
  flex-direction: column;
}
.about-me .langs .container .lang {
  position: relative;
  height: 30px;
}
.about-me .langs .container .lang .lang-name {
  position: absolute;
  left: 50%;
  top: 50%;

  color: var(--white);
  transform: translate(-50%, -50%);
}
.about-me .langs .container .lang .lang-score {
  width: 100%;
  height: 30px;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 10px;
  background-color: var(--light);
  z-index: -1;
  width: 100%;
  height: 100%;
}
.about-me .Education {
}
/* End about me */
/* Start Contact me  */
.contact-me {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 10px;
}
.contact-me a {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  font-size: 50px;
  display: block;
}
.Education .container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.Education .container .ed {
  border: 1px solid var(--dark);
  color: var(--dark);
  line-height: 1.2rem;
  padding: 5px;
  border-radius: 5px;
}
.Education .container .ed-name {
  position: relative;
}
.Education .container svg {
  color: var(--dark);
  position: absolute;
  right: 0;
  top: 90%;
  transform: translateY(-50%);
  font-size: 30px;
}
@media (max-width: 800px) {
  .about-me h2 {
    color: var(--dark);
    margin-block: calc(var(--Space) / 2);
    text-align: center;
  }
  .Home .img img {
    width: 450px;
    aspect-ratio: 1/1;
    margin-inline: auto;
    text-align: center;
    display: block;
  }
  .projects .container .item {
    width: 300px;
  }
  .contact-me {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  header nav ul {
    display: flex;
    list-style: none;
    flex-direction: row;
    justify-content: space-evenly;
    height: 60px;
    width: 100%;
  }
  .App {
    flex-direction: column-reverse;
  }
  .App > div {
    height: calc(100vh - 60px);
  }

  .Home > div {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
  header {
    width: 100vw;
    height: 60px;

    bottom: 0;
    background-color: var(--white);
    color: var(--dark);
  }
  header nav ul li:hover .icon,
  header nav ul li a + .active .icon {
    color: var(--green);
    width: 100%;
    aspect-ratio: 1/1;

    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .about-me .skills-one {
    margin-bottom: 40px;
    width: 100%;
  }
  .about-me .skills-two {
    width: 100%;
  }
  .about-me {
    overflow-y: scroll;
  }
}

@media (max-width: 676px) {
  .projects .container .item {
    width: 100%;
  }
}
